import React from 'react';
import Helmet from 'react-helmet';

const AboutPage = () =>
  <div>
    <Helmet>
      <title>Om mig - Balansera Livet</title>
      <meta name="description" content="Jag heter Helen Zetterlund och r licensierad Livsstilsrdgivare (kostrdgivare och hlsoinspiratr) med en frkrlek fr hlsa och en hlsosam livsstil." />
    </Helmet>

    <article>
      <h1>Om mig</h1>

      <ul>
        <li>Certifierad ICC-coach</li>
        <li>Certifierad Livsstilsrdgivare</li>
        <li>Certifierad Kostrdgivare</li>
      </ul>
    </article>
  </div>

export default AboutPage;



