import React from 'react';
import Helmet from 'react-helmet';
import Product from '../components/Product';

const products = require('../data/products.json');

const ProductsPage = () =>
  <div>
    <Helmet title="Tjnster - Balansera Livet" />

    <article>
      <h1>Tjnster</h1>

      <ul>
        <li>Livscoaching</li>
        <li>Hlsocoaching</li>
        <li>Karrirscoaching</li>
      </ul>

      <h2>Coaching hjlper dig i din resa</h2>

      <ul>
        <li>Vad r det du hoppas p och drmmer om?</li>
        <li>Vad r viktigt fr dig?</li>
        <li>Har du allt som du frtjnar?</li>
        <li>Vad r mjligt fr dig?</li>
      </ul>

      <p>Frestll dig ett stt att utforska dessa frgor tillsammans med ngon som hjlper dig att frverkliga dina bsta drmmar s att du blir mer av den personen du alltid velat vara. Detta r coaching.</p>

      <p>Det r inte s konstigt att coaching har blivit s populrt. Vi lever i en spnnande tid. Vi har fantastiska mjligheter och vill gra det mesta av dem. Vi knner att vi frtjnar lycka. Vi r villiga att investera tid, anstrngningar och pengar i att frbttra oss sjlva och f mer tillfredsstllelse i allt det vi gr. </p>

      <p>En coach r din guide p vgen till det bsta du kan vara.</p>
    </article>
  </div>

export default ProductsPage;



